/* header start */
.header {
  @include transition(all 0.3s);
  &__top {
    background-color: #f8fafb;
    padding: 10px 0;
    @media (max-width: 767px) {
      padding: 5px 0; }
    .top-menu {
      @include d-flex;
      li {
        margin: 0 10px;
        a {
          color: $heading-color;
          &:hover {
            color: $base-color; } } } } }

  &__bottom {
    background-color: #000000;
    border-bottom: 1px solid rgba(#e5e5e5, 0.09);
    @include transition(all 0.3s);
    @media (max-width: 1199px) {
      padding: 10px 0; } }
  .site-logo {
    img {
      width: 175px;
      @media (max-width: 1199px) {
        max-width: 150px; } } }
  .main-menu {
    @media (max-width: 1199px) {
      padding: 15px 0; }
    li {
      position: relative;
      &.menu_has_children {
        position: relative;
        &.open {
          .sub-menu {
            display: block; } }
        >a {
          @media (max-width: 1199px) {
            display: block; }
          &::before {
            position: absolute;
            content: "\f107";
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            top: 0;
            right: 0;
            color: #ffffff;
            @include transition(all 0.3s);
            display: none;
            @media (max-width: 1199px) {
              display: block;
              top: 9px; } } }
        &:hover {
          >a::before {
            @include transform(rotate(180deg));
            color: $base-color; } } }
      a {
        padding: 25px 15px 25px 0;
        text-transform: uppercase;
        font-size: 14px;
        color: #ffffff;
        position: relative;
        @media (max-width: 1199px) {
          color: #ffffff;
          padding: 8px 0;
          display: block;
          border-bottom: 1px solid rgba(#ffffff, 0.1); }
        &:hover,&:focus {
          color: $base-color; } }
      .sub-menu {
        position: absolute;
        width: 220px;
        top: 105%;
        left: -20px;
        z-index: 9999;
        background-color: #ffffff;
        padding: 10px 0;
        -webkit-box-shadow: 0px 18px 54px -8px rgba(0,0,0,0.15);
        box-shadow: 0px 18px 54px -8px rgba(0,0,0,0.15);
        @include border-radius(5px);
        @include transition(all 0.3s);
        opacity: 0;
        visibility: hidden;
        border: 1px solid #e5e5e5;
        @media (max-width: 1199px) {
          opacity: 1;
          visibility: visible;
          display: none;
          position: static;
          @include transition(none);
          width: 100%; }

        li {
          a {
            padding: 8px 20px;
            display: block;
            color: $para-color;
            font-size: 14px;
            &:hover {
              background-color: rgba(#ffffff, 0.05);
              color: $base-color;
              text-decoration: underline; } } }
        li+li {
          margin-left: 0; } }
      &:hover .sub-menu {
        top: 100%;
        opacity: 1;
        visibility: visible; } }
    li+li {
      margin-left: 20px;
      @media (max-width: 1199px) {
        margin-left: 0; } } }
  .nav-right {
    margin-left: 30px;
    @include d-flex;
    align-items: center;
    @media (max-width: 1199px) {
      margin-left: 0; }
    .niceSelect {
      width: auto;
      background-color: transparent;
      margin-left: 15px;
      .current {
        color: #ffffff; } }
    .signin-btn {
      padding: 8px 15px;
      color: $para-color;
      border: 1px solid #ffffff;
      color: #ffffff;
      @include border-radius(5px);
      @media (max-width: 1199px) {
        color: #ffffff; } }
    .login-btn {
      padding: 8px 15px;
      color: #ffffff;
      background-color: $base-color;
      border: 1px solid $base-color;
      @include border-radius(5px);
      margin-left: 15px; }
    .header-serch-btn {
      text-align: right;
      border: none;
      background-color: transparent;
      color: $base-color;
      cursor: pointer;
      font-size: 18px;
      margin-right: 15px;
      @media (max-width: 1199px) {
        display: none; } }
    .header-top-search-area {
      position: absolute;
      top: 105%;
      width: 350px;
      right: 0;
      opacity: 0;
      visibility: hidden;
      z-index: 999;
      @include transition(all 0.3s);
      @media (max-width: 1199px) {
        position: static;
        opacity: 1;
        visibility: visible;
        margin-top: 20px;
        margin-bottom: 15px;
        width: 100%; }
      .header-search-form {
        position: relative;
        background-color: #ffffff;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.15);
        @include border-radius(999px);
        @media (max-width: 1199px) {
          width: 50%; }
        @media (max-width: 767px) {
          width: 100%; }
        input {
          width: 100%;
          border: none;
          padding: 8px 38px 8px 15px;
          min-height: 50px;
          @include border-radius(999px); }
        .header-search-btn {
          position: absolute;
          top: 0;
          right: 0;
          border: none;
          height: 100%;
          padding: 0 12px;
          background-color: $base-color;
          color: #ffffff;
          @include border-radius(0 999px 999px 0); } }
      &.open {
        opacity: 1;
        visibility: visible;
        top: 100%; } } } }

.navbar-collapse {
  @media (max-width: 1199px) {
    background-color: $bg-one;
    padding: 0 30px 20px 30px; }
  @media (max-width: 767px) {
    max-height: 320px;
    overflow: auto; } }
.navbar-toggler {
  padding: 0;
  border: none;
  &:focus {
    outline: none; }
  @media (max-width: 1199px) {
    margin-right: 20px; } }
.menu-toggle {
  margin: 10px 0;
  position: relative;
  display: block;
  width: 35px;
  height: 20px;
  cursor: pointer;
  background: transparent;
  border-top: 2px solid;
  border-bottom: 2px solid;
  color: #ffffff;
  font-size: 0;
  @include transition(all 0.25s ease-in-out);
  cursor: pointer;
  @media (max-width: 1199px) {
    color: #ffffff; } }
.menu-toggle:before, .menu-toggle:after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  background: currentColor;
  @include transform(translate(-50%, -50%));
  transition: -webkit-transform 0.25s ease-in-out;
  -webkit-transition: -webkit-transform 0.25s ease-in-out;
  -o-transition: -webkit-transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out;
  -moz-transition: -webkit-transform 0.25s ease-in-out;
  -ms-transition: -webkit-transform 0.25s ease-in-out;
  @media (max-width: 1199px) {
    background-color: #ffffff; } }
span.is-active {
  border-color: transparent; }
span.is-active:before {
  @include transform(translate(-50%, -50%) rotate(45deg)); }
span.is-active:after {
  @include transform(translate(-50%, -50%) rotate(-45deg)); }
span.menu-toggle:hover {
  color: #ffffff;
  @media (max-width: 1199px) {
    color: #ffffff; } }
span.is-active {
  border-color: transparent; }
span.is-active:before {
  @include transform(translate(-50%, -50%) rotate(45deg)); }
span.is-active:after {
  @include transform(translate(-50%, -50%) rotate(-45deg)); }
/* header end */
