/* category section css start */
.category-section {
  position: relative;
  margin-top: -120px; }
.category-card {
  box-shadow: 0 5px 15px rgba(#000000, 0.15);
  &:hover {
    .category-card__thumb {
      img {
        @include transform(scale(1.1)); } } }
  &__thumb {
    position: relative;
    overflow: hidden;
    max-height: 240px;
    @include border-radius(5px);
    z-index: 2;
    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #000000;
      opacity: 0.35;
      z-index: 1; }
    img {
      @include transition(all 0.3s); } }
  &__content {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 20px 30px;
    z-index: 2;
    .title {
      color: #ffffff; }
    .total-amount {
      font-size: 14px;
      color: rgba(#ffffff, 0.8); } } }
.category-slider {
  .slick-list {
    margin: -15px -10px; }
  .single-slide {
    margin: 15px 10px; }
  .slick-arrow {
    position: absolute;
    top: 50%;
    width: 40px;
    height: 40px;
    margin-top: -20px;
    background-color: #ffffff;
    color: $heading-color;
    font-size: 24px;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    @include border-radius(50%);
    box-shadow: 0 0 10px rgba(#000000, 0.25);
    @include transition(all 0.3s);
    &:hover {
      background-color: $base-color;
      color: #ffffff; }
    &.prev {
      left: -20px;
      @media (max-width: 1440px) and (min-width: 1200px) {
        left: 0; }
      @media (max-width: 575px) {
        left: 0; } }
    &.next {
      right: -20px;
      @media (max-width: 1440px) and (min-width: 1200px) {
        right: 0; }
      @media (max-width: 575px) {
        right: 0; } } } }
/* category section css end */


/* photo section css start */
.photo-gallery {
  margin-left: -7px;
  margin-right: -7px;
  margin-bottom: -14px;
  [class*="col"] {
    padding-left: 7px;
    padding-right: 7px; }
  .photo-card {
    margin-bottom: 14px; } }
.photo-card {
  height: 100%;
  overflow: hidden;
  height: 350px;
  @include border-radius(5px);
  position: relative;
  &:hover {
    &::before {
      opacity: 0.65; }
    .photo-card__content {
      opacity: 1;
      visibility: visible;
      bottom: 0;
      transition-delay: 0.2s; }
    .collection-meta {
      opacity: 1;
      visibility: visible;
      top: 20px;
      transition-delay: 0.2s; } }
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0;
    @include transition(all 0.2s);
    transition-delay: 0.2s; }
  &__thumb {
    width: 100%;
    height: 100%;
    @include object-fit; }
  &__content {
    @include d-flex;
    justify-content: space-between;
    position: absolute;
    width: 100%;
    bottom: -70px;
    left: 0;
    padding: 20px;
    opacity: 0;
    visibility: hidden;
    @include transition(all 0.3s); }
  .author {
    @include d-flex;
    align-items: center;
    img {
      width: 35px;
      height: 35px;
      @include object-fit;
      @include border-radius(50%); }
    .name {
      font-size: 14px;
      color: #ffffff;
      margin-left: 5px;
      &:hover {
        color: $base-color; } } }
  .action-area {
    @include d-flex;
    margin: -3px -5px;
    li {
      margin: 3px 5px;
      color: #ffffff;
      i {
        font-size: 20px; }
      .total-count {
        font-size: 12px; } } }
  .collection-meta {
    position: absolute;
    top: 0;
    left: 20px;
    opacity: 0;
    visibility: visible;
    @include transition(all 0.3s); } }
/* photo section css end */


/* price section css start */
.section-wave-1 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  img {
    width: 100%; } }
.pricing-card {
  padding: 40px 30px 20px 30px;
  background-color: #ffffff;
  box-shadow: 0 5px 10px rgba(#000000, 0.1);
  overflow: hidden;
  @include border-radius(8px);
  @include transition(all 0.3s);
  position: relative;
  &:hover {
    @include transform(translateY(-5px));
    box-shadow: 0 10px 30px rgba(#000000, 0.1); }
  &::before {
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    width: 100%;
    height: 105px;
    background: linear-gradient(45deg, #000850 0%, #000320 100%), radial-gradient(100% 225% at 100% 0%, #FF6928 0%, #000000 100%), linear-gradient(225deg, #FF7A00 0%, #000000 100%), linear-gradient(135deg, #CDFFEB 10%, #CDFFEB 35%, #009F9D 35%, #009F9D 60%, #07456F 60%, #07456F 67%, #0F0A3C 67%, #0F0A3C 100%);
    background-blend-mode: screen, overlay, hard-light, normal; }
  &__icon {
    width: 95px;
    height: 95px;
    background-color: #ffffff;
    border: 1px solid #F9F9FB;
    margin: 0 auto;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include border-radius(50%);
    position: relative;
    z-index: 1;
    box-shadow: 0 5px 10px rgba(#000000, 0.15);
    &::before {
      position: absolute;
      content: '';
      width: calc(100% - 14px);
      height: calc(100% - 14px);
      top: 7px;
      left: 7px;
      background-color: #F9F9FB;
      @include border-radius(50%);
      z-index: -1; }
    i {
      font-size: 52px;
      color: #000000; } }
  &__content {
    margin-top: 20px;
    .package-name {
      margin-bottom: 20px; }
    .package-amount {
      font-size: 32px;
      font-weight: 700;
      color: lighten($para-color, 30%);
      margin-top: 30px;
      small {
        font-size: 14px;
        color: #000000; } } }
  &__list {
    li {
      padding: 10px 0;
      border-bottom: 1px dashed #e5e5e5;
      &:first-child {
        padding-top: 0; }
      &:last-child {
        padding-bottom: 0;
        border-bottom: none; }
      &::before {
        content: "\f00c";
        font-family: 'Line Awesome Free';
        font-weight: 900;
        color: #1bcc87;
        margin-right: 5px; }
      &.deactive {
        &::before {
          content: "\f00d";
          color: #de4463; } } } } }
/* price section css end */


/* choose us section css start */
.choose-card {
  @media (max-width: 575px) {
    text-align: center; }
  &__icon {
    margin-bottom: 20px;
    img {
      max-width: 65px; } } }
