/* testimonial section css start */
.testimonial-card {
  padding: 30px 30px;
  background-color: #ffffff;
  @include border-radius(5px);
  overflow: hidden;
  position: relative;
  box-shadow: 0 5px 15px rgba(#000000, 0.05);
  &::before {
    position: absolute;
    content: "\f10e";
    font-family: 'Line Awesome Free';
    font-weight: 900;
    bottom: -10px;
    right: 10px;
    font-size: 72px;
    line-height: 1;
    opacity: 0.15; } }
.testimonial-slider {
  .slick-list {
    margin: -15px; }
  .single-slide {
    margin: 15px; }
  .slick-arrow {
    position: absolute;
    top: 50%;
    width: 40px;
    height: 40px;
    margin-top: -20px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    color: #000000;
    box-shadow: 0 0 10px rgba(#000000, 0.15);
    @include border-radius(50%);
    z-index: 1;
    &.prev {
      left: -30px;
      @media (max-width: 1440px) and (min-width: 1024px) {
        left: -10px; } }
    &.next {
      right: -30px;
      @media (max-width: 1440px) and (min-width: 1024px) {
        right: -10px; } } } }
/* testimonial section css end */


/* cta section css start */
.cta-section {
  padding-top: 120px;
  padding-bottom: 120px;
  background-color: darken($base-color , 2%);
  position: relative;
  overflow: hidden;
  .wave {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%; } }
/* cta section css end */


/* photo details css start */
.photo-details-thumb {
