.related-photo-slider {
  .slick-list {
    margin: 0 -10px; }
  .single-slide {
    margin: 0 10px; }
  .slick-arrow {
    position: absolute;
    top: 50%;
    width: 40px;
    height: 40px;
    margin-top: -20px;
    background-color: #ffffff;
    color: $heading-color;
    font-size: 24px;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    @include border-radius(50%);
    box-shadow: 0 0 10px rgba(#000000, 0.25);
    @include transition(all 0.3s);
    &:hover {
      background-color: $base-color;
      color: #ffffff; }
    &.prev {
      left: -20px;
      @media (max-width: 1440px) and (min-width: 1200px) {
        left: 0; }
      @media (max-width: 575px) {
        left: 0; } }
    &.next {
      right: -20px;
      @media (max-width: 1440px) and (min-width: 1200px) {
        right: 0; }
      @media (max-width: 575px) {
        right: 0; } } } }

.comments-area {
  margin-top: 100px;
  margin-right: 30px;
  @include border-radius(8px);
  .title {
    text-transform: capitalize;
    margin-bottom: 20px;
    font-size: 24px; } }
.comments-list {
  .single-comment {
    display: flex;
