  color: $base-color;
  text-align: center;
  line-height: 28px;
  font-size: 13px;
  float: right;
  margin-top: -30px;
  @include border-radius(50%);
  &:hover {
    background-color: $base-color;
    color: #ffffff; }
  i {
    @include transform(rotate(180deg)); } }
.comment-form-area {
  margin-top: 70px;
  margin-right: 30px;
  .title {
    font-size: 24px;
    margin-bottom: 20px; } }
/* photo details css end */


/* photo comment sidebar css start */
#photo-comment-sidebar {
  padding: 50px 30px 30px 30px;
  background-color: #ffffff;
  width: 550px;
  height: 100vh;
  overflow: auto;
  position: fixed;
  top: 0;
  right: -100%;
  z-index: 9999;
  box-shadow: 0 0 10px rgba(#000000, 0.25);
  @include transition(all 0.5s);
  @media (max-width: 575px) {
    width: 100%; }
  &.active {
    right: 0; }
  #commentCloseBtn {
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 32px; } }
/* photo comment sidebar css end */


/* author-section css start */
.author-section {
  .image-holder {
    height: 250px; }
  .author {
    margin-top: -65px;
    @include d-flex;
    @media (max-width: 767px) {
      justify-content: center; }
    &__thumb {
      width: 120px;
      height: 120px;
      @include border-radius(50%);
      overflow: hidden;
      border: 4px solid #ffffff;
      img {
        width: inherit;
        height: inherit; } }
    &__content {
      width: calc(100% - 120px);
      padding-left: 30px;
      @media (max-width: 767px) {
        width: 100%;
        padding-left: 0;
        margin-top: 20px; }
      .stat-list {
        padding: 10px 0;
        border: none;
        background-color: rgba(#000000, 0.65);
        @media (max-width: 575px) {
          @include d-flex; }
        li {
          padding: 0;
          border-color: rgba(#ffffff, 0.5);
          @media (max-width: 575px) {
            padding: 10px 15px;
            border: none; }
          .stat-amount {
            color: #ffffff; }
          .caption {
            color: rgba(#ffffff, 0.8); }
          a {
            &:hover {
              color: $base-color;
              text-decoration: underline; } } } }
      .name {
        i {
          color: golden; } } }
    .content-area {
      @include d-flex;
      align-items: center;
      @media (max-width: 767px) {
        text-align: center; }
      .right {
        padding-left: 30px;
        @media (max-width: 1199px) {
          width: 100%;
          padding-left: 0;
          margin-top: 20px; } } }
    &__meta {
      @include d-flex;
      margin: -5px -10px;
      li {
        padding: 5px 10px;
        i {
          &.las, &.lar {
            font-size: 18px; } }
        a {
          color: $para-color;
          &:hover {
            color: $base-color; } } } } } }
.text-link {
  &:hover {
    color: $base-color !important; } }
/* author-section css end */


/* acount section css start */
.acount-wrapper {
  overflow: hidden;
  @include border-radius(5px);
  box-shadow: 0px 0px 30px 9px rgba(0, 0, 0, 0.05);
  @media (max-width: 575px) {
    margin: 0 15px; } }
.acount-area {
  padding: 50px;
  @media (max-width: 575px) {
    padding: 30px; }
  @media (max-width: 480px) {
    padding: 30px 15px; } }
/* acount section css end*/


/* members section css start */
.member-card {
  background-color: #ffffff;
  padding: 15px;
  @include border-radius(8px);
  box-shadow: 0 5px 15px rgba(#000000, 0.1);
  &__thumb {
    img {
      width: 100%;
      @include border-radius(5px); } }
  &__content {
    padding: 20px;
    text-align: center;
    .stat-list {
      border: none;
      background-color: #f1f1f1;
      padding: 8px 0;
      li {
        padding: 0; } } } }
/* members section css end */


/* upload section css start */
.upload-wrapper {
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(#000000, 0.15);
  @include border-radius(8px);
  overflow: hidden;
  padding: 40px 0; }
.photo-upload-area {
  background-color: #ffffff;
  padding: 0 30px;
  height: 100%;
  @include d-flex;
  align-items: center; }
.file-upload {
  background-color: #ffffff;
  margin: 0 auto;
  .file-upload-btn {
    width: 100%;
    margin: 0;
    color: #fff;
    background: #0062FF;
    border: none;
    padding: 10px;
    border-radius: 4px;
    border-bottom: 4px solid darken(#0062FF, 10%);
    transition: all .2s ease;
    outline: none;
    text-transform: uppercase;
    font-weight: 700;
    &:hover {
      background: darken(#0062FF, 5%);
      color: #ffffff;
      transition: all .2s ease;
      cursor: pointer; }
    &.active {
      border: 0;
      transition: all .2s ease; } }
  .file-upload-content {
    display: none;
    text-align: center; }
  .file-upload-input {
    position: absolute;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    outline: none;
    opacity: 0;
    cursor: pointer; }
  .image-upload-wrap {

    position: relative;
    border: 2px dashed #dddddd;
    @include border-radius(5px); }
  .image-dropping,
  .image-upload-wrap {
    &:hover {
      background-color: darken(#f1f1f1, 5%); } }
  .image-title-wrap {
    padding: 0 15px 15px 15px;
    olor: #222; }
  .drag-text {
    text-align: center;
    padding: 150px 50px;
    .title {
      font-size: 22px;
      color: #6d6d6d;
      text-shadow: 0 5px 5px rgba(#000000, 0.15); } }
  .file-upload-image {
    width: 100%;
    @include border-radius(5px); }
  .remove-image {
    width: 200px;
    margin: 0;
    color: #fff;
    background: #cd4535;
    border: none;
    padding: 10px;
    border-radius: 4px;
    border-bottom: 4px solid #b02818;
    transition: all .2s ease;
    outline: none;
    text-transform: uppercase;
    font-weight: 700;
    &:hover {
      background: #c13b2a;
      color: #ffffff;
      transition: all .2s ease;
      cursor: pointer; }
    &.active {
      border: 0;
      transition: all .2s ease; } } }
.photo-upload-content-area {
  height: 100%;
  padding: 0 30px;
  border-left: 1px solid #e5e5e5;
  @media (max-width: 991px) {
    border-left: none;
    margin-top: 50px; } }
/* upload section css end */


/* dashboard section css start */
.d-widget-one {
  padding: 20px 10px;
  background-color: $base-color;
  border: 1px solid $border-color;
  @include border-radius(5px);
  box-shadow: 0 10px 10px 0 rgba($base-color, 0.15);
  .caption {
    color: rgba(#ffffff, 0.9);
    font-size: 16px; }
  span, p {
    font-size: 14px; }
  .currency-amount {
    font-size: 32px;
