    .icon {
      background-color: rgba(#10163a, 0.15);
      color: #10163a; }
    .amount {
      color: #10163a; } } }


.author-widget {
  padding: 50px 30px 30px 30px;
  background-color: #ffffff;
  box-shadow: 0 0 10px rgba(#000000, 0.05);
  @include border-radius(5px);
  position: relative;
  z-index: 1;
  overflow: hidden;
  &__wave {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 115px;
    display: flex;
    align-items: flex-end;
    z-index: -1;
    background-color: $base-color;
    img {
      width: 100%; } }
  &__thumb {
    width: 110px;
    height: 110px;
    border: 4px solid #ffffff;
    overflow: hidden;
    margin: 0 auto;
    @include border-radius(50%);
    box-shadow: 0 0 5px rgba(#000000, 0.3);
    img {
      width: inherit;
      height: inherit; } }
  &__list {
    margin-top: 30px;
    li {
      border-bottom: 1px dashed #e5e5e5;
      a {
        color: $para-color;
        padding: 10px 0;
        display: flex;
        align-items: center;
        &:hover {
          color: $base-color; }
        i {
          font-size: 22px;
          margin-right: 8px;
          text-shadow: 0 5px 5px rgba(#000000, 0.25); } } } } }
/* dashboard section css end */


/* contact-wrapper css start */
.contact-wrapper {
  overflow: hidden;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(#000000, 0.15);
  @include border-radius(5px); }
.contact-form-wrapper {
  padding: 50px;
  @media (max-width: 575px) {
    padding: 30px; } }
.contact-item {
  i {
    color: #000000; }
  a {
    color: $para-color;
    &:hover {
      color: $base-color; } } }
/* contact-wrapper css end */
