/* form css start */
.form-control {
  padding: 10px 20px;
  border: 1px solid $border-color;
  width: 100%;
  background-color: {}
  @include placeholder(rgba(#000000, 0.45));
  @include border-radius(3px);
  color: $para-color;
  min-height: 50px;
  &:focus {
    box-shadow: none; } }
select {
  padding: 10px 20px;
  width: 100%;
  border: 1px solid $border-color;
  cursor: pointer;
  color: $para-color;
  background-color: transparent;
  height: 50px;
  @include border-radius(4px); }
textarea {
  min-height: 150px !important;
  resize: none;
  width: 100%; }
.form-group {
  margin-bottom: 20px; }
select {
  option {
    background-color: $bg-two; } }
label {
  color: #777777;
  font-size: 14px;
  margin-bottom: 8px; }
.nice-select {
  float: none;
  border: 1px solid $border-color;
  .list {
    max-height: 250px;
    overflow: auto;
    &::-webkit-scrollbar {
      width: 8px; }
    &::-webkit-scrollbar-track {
      box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }
    &::-webkit-scrollbar-thumb {
      background-color: darkgrey;
      outline: 1px solid slategrey; } } }
.custom-radio {
  position: relative;
  cursor: pointer;
  @include d-flex;
  align-items: center;
  justify-content: space-between; }
.custom-radio {
	&__field {
		position: absolute;
		left: -9999px;
		&:checked + .custom-radio__circle {
			// background-color: mix(#fff, $base-color, 84%)
			&:before {
				box-shadow: inset 0 0 0 5px $base-color; } } }
	&__circle {
		@include border-radius(50%);
		transition: 0.25s ease;
		// &:hover
		// 	background-color: mix(#fff, $base-color, 84%)
		&:before {
			display: flex;
			flex-shrink: 0;
			content: "";
			background-color: #fff;
			width: 15px;
			height: 15px;
			border-radius: 50%;
			margin-right: 0.375em;
			transition: 0.25s ease;
			box-shadow: inset 0 0 0 2px $base-color; }
  .value {
    float: right; } } }
.select2 {
  display: block;
  height: 50px;
  .selection {
    display: block; }
  .select2-selection , .select2-container {
    height: 50px; }
  .select2-selection {
    border-color: $border-color; } }
/* form css end*/


