/* hero css start */
.hero {
  padding-top: 150px;
  padding-bottom: 250px;
  position: relative;
  @media (max-width: 575px) {
    padding-top: 90px;
    padding-bottom: 190px; }
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0.25; }
  &__title {
    font-size: 36px;
    color: #ffffff;
    font-weight: 700;
    margin-bottom: 20px;
    @media (max-width: 991px) {
      font-size: 28px; }
    @media (max-width: 767px) {
      font-size: 24px; } }
  &__search-form {
    position: relative;
    .search-form-label {
      width: 100%; }
    .label-txt {
      position: absolute;
      top: 17px;
      left: 20px;
      color: rgba(#000000, 0.45);
      opacity: 1;
      &.label-active {
        display: none; } }
    .form-control {
      height: 60px; }
    .search-btn {
      position: absolute;
      top: 10px;
      right: 20px;
      background-color: transparent;
      width: auto;
      color: $para-color;
      font-size: 20px; } } }
/* hero css end */


/* inner hero css start */
.inner-hero {
  padding-top: 100px;
  padding-bottom: 100px;
  position: relative;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0.35; }
  &__title {
    color: #ffffff; } }
/* inner hero css end */
