/* sidebar css start */
.widget {
  padding: 30px;
  border: 2px solid #e5e5e5;
  @include border-radius(5px);
  &__title {
    margin-bottom: 30px;
    position: relative;
    &::after {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 25px;
      height: 2px;
      background-color: $base-color; } } }

.widget + .widget {
  margin-top: 30px; }
.stat-list {
  display: table;
  width: 100%;
  border: 1px solid #e5e5e5;
  @include border-radius(5px);
  li {
    display: table-cell;
    text-align: center;
    border-right: 1px solid #e5e5e5;
    padding: 10px 0;
    &:last-child {
      border-right: none; }
    .stat-amount {
      font-weight: 700;
      font-size: 18px;
      line-height: 1.1; }
    .caption {
      font-size: 14px; } } }
.details-list {
  li {
    padding: 10px 0;
    @include d-flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px dashed #e5e5e5;
    &:first-child {
      padding-top: 0; }
    .caption {
      font-weight: 700; } } }
.tags {
  .single-tag {
    font-size: 14px;
    color: $para-color;
    padding: 8px 25px;
    border: 1px solid #e5e5e5;
    @include border-radius(999px);
    margin: 5px 2px;
    @include transition(all 0.3s);
    &:hover {
      background-color: $base-color;
      color: #ffffff;
      border-color: $base-color; } } }
.image-action-meta {
  @include d-flex;
  justify-content: space-between; }
.licence-widget {
  .title {
    font-size: 15px;
    margin-bottom: 10px; }
  .licence-text {
    a {
      color: $base-color; } } }
/* sidebar css end */
