/* button css start */
.cmn-btn {
  padding: 12px 35px;
  text-transform: uppercase;
  @include border-radius(5px);
  background-color: $base-color;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.15);
  @include transition(all 0.3s);
  color: #ffffff;
  font-size: 15px;
  &:hover {
    color: #ffffff;
    background-color: darken($base-color, 5%); }
  &.active {
    background-color: $bg-one; } }
.cmn-btn2 {
  padding: 12px 35px;
  text-transform: uppercase;
  @include border-radius(5px);
  background-color: $base-color-two;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.15);
  @include transition(all 0.3s);
  color: #ffffff;
  &:hover {
    color: #ffffff;
    background-color: darken($base-color, 5%); } }
.border-btn {
  padding: 12px 25px;
  font-weight: 600;
  color: $para-color;
  @include border-radius(5px);
  border: 1px solid #e5e5e5;
  @include transition(all 0.3s);
  &:hover {
    background-color: $base-color;
    color: #ffffff;
    border-color: $base-color;
    box-shadow: 0 5px 10px rgba(#000000, 0.05); } }
.read-btn {
  padding: 10px 20px 10px 30px;
  min-width: 150px;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  background-color: rgba($base-color, 0.14);
  color: $base-color;
  position: relative;
  text-align: center;
  @include border-radius(5px);
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  @include transition(all 0.3s);
  &:hover {
    background-color: $base-color;
    color: #ffffff;
    i {
      background-color: #ffffff;
      color: $base-color; } }
  i {
    background-color: $base-color;
    color: #ffffff;
    font-size: 18px;
    margin-left: 15px;
    padding: 6px 6px;
    @include border-radius(5px);
    @include transition(all 0.3s); } }

.btn-group {
  @include d-flex;
  margin-left: -10px;
  margin-right: -10px;
  *[class*="btn"] {
    margin: 5px 10px;
    align-items: center;
    &.d-flex {
      padding: 8px 35px; } } }
*[class*="btn"] {
  &.btn-sm {
    font-size: 14px;
    padding: 8px 20px !important; } }

.action-btn {
  padding: 5px 15px;
  font-weight: 600;
  background-color: #ffffff;
  color: $base-color;
  font-size: 14px;
  @include border-radius(5px); }
/* button css end */
