/* footer section css start */
.footer {
  background-color: #000000;
  p, span, li, i, a {
    color: rgba(#ffffff, 0.8); }
  &__top {
    padding-top: 120px;
    padding-bottom: 100px; }
  &__bottom {
    border-top: 1px solid rgba(#ffffff, 0.25);
    padding: 20px 0; } }
.footer-widget {
  &__title {
    color: #ffffff;
    margin-bottom: 25px; } }
.social-list {
  @include d-flex;
  margin: -5px;
  li {
    margin: 5px;
    a {
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      @include border-radius(50%);
      border: 1px solid #e5e5e5;
      @include transition(all 0.3s);
      &:hover {
        background-color: $base-color;
        border-color: $base-color;
        color: #ffffff; } } } }
.short-links {
  li + li {
    margin-top: 8px; }
  a {
    position: relative;
    padding-left: 15px;
    @include transition(all 0.2s);
    &:hover {
      color: $base-color;
      &::before {
        background-color: $base-color; } }
    &::before {
      position: absolute;
      content: '';
      top: 9px;
      left: 0;
      width: 6px;
      height: 6px;
      background-color: rgba(#ffffff, 0.8);
      @include border-radius(50%);
      @include transition(all 0.2s); } } }
.subscribe-form {
  position: relative;
  .form-control {
    padding-right: 50px; }
  .subscribe-btn {
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 100%;
    background-color: $base-color;
    color: #ffffff;
    font-size: 22px;
    @include border-radius(0 3px 3px 0); } }
.short-menu-list {
  @include d-flex;
  margin: -5px -15px;
  li {
    padding: 5px 15px;
    position: relative;
    &:last-child {
      &::after {
        display: none; } }
    &::after {
      position: absolute;
      content: '';
      top: 50%;
      right: 0;
      margin-top: -5px;
      height: 10px;
      width: 1px;
      background-color: rgba(#ffffff, 0.8); } } }
/* footer section css end */
